<template>
	<div class="icontent">
		<el-row class="ititle">
			<el-col :span="24">
				<span class="pt20">标题表格</span>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="24">
				<el-form :inline="true" :model="formInline" class="demo-form-inline">
					<el-form-item label="姓名">
   						<el-input v-model="formInline.user" placeholder="姓名"></el-input>
  					</el-form-item>
  					<el-form-item label="地址">
   						<el-input v-model="formInline.address" placeholder="地址"></el-input>
  					</el-form-item>
  					<el-form-item>
				    	<el-button type="primary">查询</el-button>
				  	</el-form-item>
				</el-form>
			</el-col>
		</el-row>
		<el-row>	
			<el-table :data="tableData3" border style="width: 100%" @selection-change="handleSelectionChange">
		    <el-table-column type="selection" width="55">
		    </el-table-column>
		    <el-table-column label="日期" width="120">
		      <template scope="scope">{{ scope.row.date }}</template>
		    </el-table-column>
		    <el-table-column prop="name" label="姓名" width="120">
		    </el-table-column>
		    <el-table-column prop="address" label="地址" show-overflow-tooltip>
		    </el-table-column>
		    <el-table-column label="操作">
		      <template scope="scope">
		        <el-button
		          size="small"
		          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
		        <el-button
		          size="small"
		          type="danger"
		          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
		      </template>
		    </el-table-column>
		  </el-table>
		 </el-row>

		 <el-row>
		 	<div class="block">
			  <el-pagination
			    layout="prev, pager, next"
			    :total="50">
			  </el-pagination>
			</div>
		 </el-row>
		
		    <el-dialog title="修改个人信息" v-model="dialogFormVisible" size="tiny">
		      <el-form ref="form" :model="form" label-width="80px">
		      	<el-form-item label="日期">
		        	<el-date-picker type="date" placeholder="选择日期" v-model="form.date" style="width: 100%;" ></el-date-picker>
		        </el-form-item>
		        <el-form-item label="姓名">
		          <el-input v-model="form.name"></el-input>
		        </el-form-item>
		        <el-form-item label="地址">
		          <el-input v-model="form.address"></el-input>
		        </el-form-item>
		        <el-form-item>
		        	<el-button type="primary" @click="handleSave" :loading="editLoading">修改</el-button>
		        	<el-button @click="dialogFormVisible=false">取消</el-button>
		        </el-form-item>
		      </el-form>
		</el-dialog>
		
		

	</div>
</template>
<script>
	export default{
		 data() {
		      return {
		        tableData3: [{
		          date: '2016-05-03',
		          name: '王小虎',
		          address: '上海市普陀区金沙江路 1518 弄'
		        }, {
		          date: '2016-05-02',
		          name: '王小虎',
		          address: '上海市普陀区金沙江路 1518 弄'
		        }, {
		          date: '2016-05-04',
		          name: '王小虎',
		          address: '上海市普陀区金沙江路 1518 弄'
		        }, {
		          date: '2016-05-01',
		          name: '王小虎',
		          address: '上海市普陀区金沙江路 1518 弄'
		        }, {
		          date: '2016-05-08',
		          name: '王小虎',
		          address: '上海市普陀区金沙江路 1518 弄'
		        }, {
		          date: '2016-05-06',
		          name: '王小虎',
		          address: '上海市普陀区金沙江路 1518 弄'
		        }, {
		          date: '2016-05-07',
		          name: '王小虎',
		          address: '上海市普陀区金沙江路 1518 弄'
		        }],
		        formInline:{
		        	user:''
		        },
		        multipleSelection: [],
		        editLoading: false,
		        dialogFormVisible: false,
		        form:{
		        	date:'',
		        	name:'',
		        	address:'',
		        },
		        table_index: 999,
		      }
		    },

		    methods: {
		      handleSelectionChange(val) {
		        this.multipleSelection = val;
		      },
		      handleEdit(index, row) {
		      	this.dialogFormVisible = true;
		      	this.form = Object.assign({},row);
		      	this.table_index = index;
		      },
		      handleDelete(index, row) {
		        this.tableData3.splice(index,1);
		        this.$message({
		        	message: '操作成功!',
		        	type:'success'
		        })
		      },
		      handleSave() {
		      	this.$confirm('确认提交?','提示',{
		      		confirmButtonText:'确定',
		      		cancelButtonText:'取消',
		      		cancelButtonClass:'cancel'
		      	}).then(()=>{
		      		this.editLoading = true;
		      		let date = this.form.date;
		      		if (typeof date === "object") {
						date = [date.getFullYear(), (date.getMonth() + 1), (date.getDate())].join('-');
					    this.form.date = date;
					}
					this.tableData3.splice(this.table_index,1,this.form);
					this.$message({
						message:"操作成功!",
						type:"success"
					});
					this.editLoading = false;
					this.dialogFormVisible=false;
		      	})
		      }
		    }
}
</script>
<style type="text/stylus" lang="stylus">
	.icontent 
		padding 20px 
	.pt20
		padding-left 20px
	.ititle
		height 40px 
		line-height 40px
	.block 
		padding-top 10px  
</style>


