					  </div>
					</el-card>
				</el-col>
			</el-row>
	</div>
</template>
<script>
	export default{
		data(){
			return {
				value5:3.7
			}
		}
	}
</script>
<style type="text/stylus" lang="stylus">
	.first 
		padding 20px
	.text 
		font-size 14px 
	.item 
		padding 18px 0px 
	.box-card
		width 480px
	.mt20 
		margin-top 20px
</style>


